<template>
<div class="container">
  <div class="flex-rCenter">
    <el-form :inline="true" class="">
      <el-form-item label="日期">
        <el-date-picker style="width: 10rem;" type="date" placeholder="选择日期" v-model="addTableData.date"></el-date-picker>
      </el-form-item>
      <el-form-item label="姓名">
        <el-input style="width: 8rem;" v-model="addTableData.name" placeholder="姓名"></el-input>
      </el-form-item>
      <el-form-item label="年龄">
        <el-input style="width: 5rem;" v-model="addTableData.age" placeholder="年龄"></el-input>
      </el-form-item>
      <el-form-item label="住址">
        <el-input style="width: 16rem;" v-model="addTableData.address" placeholder="住址"></el-input>
      </el-form-item>
      <el-form-item label="电子邮箱">
        <el-input style="width: 13rem;" v-model="addTableData.email" placeholder="电子邮箱"></el-input>
      </el-form-item>
      <el-form-item label="工作经验">
        <el-input style="width: 10rem;" v-model="addTableData.workExp" placeholder="工作经验"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">新增</el-button>
      </el-form-item>
    </el-form>
  </div>
  <el-table
      :data="tableData"
      style="width: 100%">
    <el-table-column
        label="日期"
        width="180">
      <template #default="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
        label="姓名"
        width="180">
      <template #default="scope">
        <div class="name-wrapper">
          <el-tag size="medium">{{ scope.row.name }}</el-tag>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="年龄"
        width="100">
      <template #default="scope">
        <div class="name-wrapper">
          <el-tag type="success" size="medium">{{ scope.row.age }}</el-tag>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="住址"
        width="220">
      <template #default="scope">
        <div class="name-wrapper">
          <span style="margin-left: 10px">{{ scope.row.address }}</span>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="电子邮箱"
        width="180">
      <template #default="scope">
        <i class="el-icon-box"></i>
        <span style="margin-left: 10px">{{ scope.row.email }}</span>
      </template>
    </el-table-column>
    <el-table-column
        label="工作经验"
        width="180">
      <template #default="scope">
        <i class="el-icon-apple"></i>
        <span style="margin-left: 10px">{{ scope.row.workExp }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template #default="scope">
        <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog
      title="编辑"
      v-model="editDialogVisible"
      width="30%"
      :append-to-body="true"
  >
    <el-form class="flex-cCenter">
      <table cellspacing="20">
        <tr>
          <td><lable style="margin-right: 12px">日期</lable></td>
          <td>
            <el-date-picker style="width: 16rem;" type="date" placeholder="选择日期" v-model="editTableData.date"></el-date-picker>
          </td>
        </tr>
        <tr>
          <td><lable style="margin-right: 12px">姓名</lable></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.name" placeholder="姓名"></el-input>
          </td>
        </tr>
        <tr>
          <td><lable style="margin-right: 12px">年龄</lable></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.age" placeholder="年龄"></el-input>
          </td>
        </tr>
        <tr>
          <td><lable style="margin-right: 12px">住址</lable></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.address" placeholder="住址"></el-input>
          </td>
        </tr>
        <tr>
          <td><lable style="margin-right: 12px">电子邮箱</lable></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.email" placeholder="电子邮箱"></el-input>
          </td>
        </tr>
        <tr>
          <td><lable style="margin-right: 12px">工作经验</lable></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.workExp" placeholder="工作经验"></el-input>
          </td>
        </tr>
      </table>
    </el-form>
    <template #footer>
    <span class="dialog-footer">
      <el-button @click="editDialogVisible=false">取 消</el-button>
      <el-button type="primary" @click="handleEditDone">确 定</el-button>
    </span>
    </template>
  </el-dialog>
</div>
</template>

<script>
import { ElMessage } from 'element-plus'

export default {
  name: "BusinessManage",
  data() {
    return {
      editDialogVisible: false,
      addTableData: {
        date: '',
        name: '',
        age: '',
        address: '',
        email: '',
        workExp: ''
      },
      editTableData: {
        index: null,
        date: '',
        name: '',
        age: '',
        address: '',
        email: '',
        workExp: ''
      },
      tableData: [
        {
          date: '2021-06-29',
          age: 22,
          name: '小虎',
          address: '湖北省武汉市 动物园',
          email: '976678954@qq.com',
          workExp: '无（正在找工作）'
        }
      ]
    }
  },
  methods: {
    handleAdd(){
      this.tableData.push({
        date: this.date?(this.date.getFullYear())+'-'+((this.date.getMonth()+1).toString().length==2?(this.date.getMonth()+1):'0'+(this.date.getMonth()+1))+'-'+((this.date.getDate().toString().length==2)?this.date.getDate():'0'+this.date.getDate()):'',
        name: this.name,
        age: this.age,
        address: this.address,
        email: this.email,
        workExp: this.workExp
      });
      ElMessage.success({
        message: '已新增一条数据',
        type: 'success'
      });
    },
    handleEdit(index, row) {
      this.editDialogVisible = true;
      this.editTableData.index = index;
      this.editTableData.date = this.tableData[index].date;
      this.editTableData.name = this.tableData[index].name;
      this.editTableData.age = this.tableData[index].age;
      this.editTableData.address = this.tableData[index].address;
      this.editTableData.email = this.tableData[index].email;
      this.editTableData.workExp = this.tableData[index].workExp;
    },
    handleEditDone(){
      let index = this.editTableData.index;
      this.editDialogVisible = false;
      this.tableData[index].date = this.editTableData.date;
      this.tableData[index].name = this.editTableData.name;
      this.tableData[index].age = this.editTableData.age;
      this.tableData[index].address = this.editTableData.address;
      this.tableData[index].email = this.editTableData.email;
      this.tableData[index].workExp = this.editTableData.workExp;
    },
    handleDelete(index, row) {
      this.tableData.splice(index, 1);
      ElMessage.success({
        message: '已删除一条数据',
        type: 'success'
      });
    }
  }
}
</script>

<style scoped>
.container{
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 5px 1px rgba(44, 62, 80, 0.2);
  padding: 1rem;
}
</style>